:host {
	position: relative;
	display: block;
	// z-index: 100;
}

div.filters-form-mobile {
	position: relative;
	background: var(--basic-1);
	// z-index: var(--z-index-base-filters);
}

div.base-filters {
	display: flex;
	align-items: center;
	gap: 4px;
	background: var(--basic-1);
	transition: padding 0.3s ease-out;

	mat-divider {
		height: 30px;
	}

	&--fixed {
		position: fixed;
		top: 0;
		width: 100%;
		margin: 0 !important;
		padding: 16px 0;
		border-bottom: 1px solid var(--neutral-2) !important;
		transition: all 0.1s ease-out;
		z-index: var(--z-index-base-filters);
	}
}
.element-row:hover {
	background: var(--general-1);
}

.badge {
	border-radius: 100px;
	width: 19px;
	height: 16px;
	display: flex;
	position: absolute;
	background: var(--sec-3);
	border: 2px solid var(--basic-1);
	display: flex;
	align-items: center;
	justify-content: center;
	top: -9px;
	right: -7px;
	transition: all 0.3s;
}
